}
.google-box{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 130px 0;
}
.google-main-box{
   width: 30%;
   margin: auto;
   text-align: center;
   box-shadow: 5px 5px 20px rgba(0,0,0,0.5);
   padding: 50px;
   border-radius: 10px;
}

.google-main-box h1{
    font-size: 2rem;
    font-weight: 600;
}

.google-btn{
    border: 2px solid #000;
    padding: 5px 20px;
    border-radius: 5px;
    background: #eb4d4b;
}
.google-btn input{
    background: transparent;
    border: 0;
    color: #fff;
    font-weight: 500;
    font-size: 30px;
}

.google-btn i {
    color: #fff;
    margin-right: 10px;
    font-size: 30px;
}

@media (max-width:600px){
    .google-main-box{
        width: 95%;
    }
    .google-btn input, .google-btn i {
        font-size: 20px;
    }
    
}


.add-service-form{
    width: 40%;
    margin: 0 auto;
    padding: 30px;
}
.add-service-form input,.add-service-form textarea{
    width: 100%;
    padding: 10px 15px;
    margin: 5px 0;
    border: 2px solid #ff4500;
    border-radius: 8px;
    outline: none;
}
.add-service-form textarea{
    resize: none;
}

.add-service-form input[type="submit"]{
    background: #dc143c;
    border: 2px solid #adff2f;
    color: #fff;
    font-weight: 600;
}

.add-service-form input:focus,.add-service-form textarea:focus{
    border-color: #adff2f;
}

@media(max-width:600px){
    .add-service-form{
        width: 95%;
        padding: 10px;
    }
}
.form-sub{
    width: 40%;
    margin: 0 auto;
    padding: 20px 0 30px;
}

.form-sub input,.form-sub textarea{
    width: 100%;
    padding: 8px 10px;
    margin: 5px 0;
    border-radius: 8px;
    outline: none;
    border: 2px solid #000;
}

.form-sub input[type="submit"]{
    background: #2ecc71;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
}

.form-sub textarea{
    resize: none;
}

.form-sub input:focus,.form-sub textarea:focus{
    border-color:#2ecc71 ;
}

.google-map iframe{
    border: 1.5px solid #000;
    padding: 5px;
    margin: 20px 0;
}

@media(max-width:600px){
    .form-sub{
        width: 95%;
    }
}
/* Banner Section Css Start Here  */

.banner-section{
    background: url(/static/media/banner-main.57578a1a.jpg);
    width: 100%;
    height: 80vh;
    background-size: cover;
    background-position: center;
    position: relative;
    z-index: 1;
}

.banner-content{
    position: absolute;
    text-align: center;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.banner-section::after{
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background:rgba(0,0,0,0.5);
    z-index: -1;
}

.banner-title{
    font-size: 4rem;
    color: #f1c40f;
}

.banner-des{
    font-size: 1.5rem;
    font-weight: 500;
    color: #e056fd;
}

/* Banner Section Css End Here  */

/* About Section Start Here  */

.main-title{
    color: #130f40;
    font-weight: 500;
    font-size: 3rem;
}

.about-des{
    font-size: 1.1rem;
    font-weight: 500;
    line-height: 1.5;
    color: #30336b;
}

/* About Section End Here  */

/* Service Secition Css Start */

.cmn-btn{
    text-decoration: none;
    background: #dc143c;
    color: #fff;
    padding: 10px 30px;
    font-size: 18px;
    font-weight: 500;
    border-radius: 10px;
    border: 2px solid #130f40;
    transition: .5s;
    margin: 8px 0;
    display: inline-block;
}
.cmn-btn i{
    margin-left: 10px;
}

.cmn-btn:hover{
    background: #2e8b57;
    color: #fff;
}

/* Service Secition Css End */

@media(max-width:600px){
    .banner-content{
       width: 100%;
       padding: 20px;
    }
    .banner-title{
        font-size: 2rem;
    }
    
    .banner-des{
        font-size: 1rem;
    }
}
